import React from 'react';
import ReactDOM from 'react-dom';

export default React.createClass({
  displayName: 'ImgGallery',
  propTypes: {
    id: React.PropTypes.string,
    class: React.PropTypes.string,
    images: React.PropTypes.array.isRequired
  },
  getDefaultProps() {
    return {
      id: '',
      class: '',
      images: []
    };
  },
  render() {
    const images = [];
    this.props.images.forEach((image, index) => {
        images.push(
            React.DOM.a({ href: image.src },
                React.DOM.img({ key: index, className: 'img-instance', src: image.src }, null)
            )
        );
      }
    );

    return (
      <div id = { this.props.id } className = { 'img-gallery ' + this.props.class }>
        {images}
      </div>
    );
  }
});



